.stats-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1.5rem;
  }
  
  .stats-table th,
  .stats-table td {
    border: 1px solid #ddd;
    padding: 0.5rem;
  }
  
  .stats-table th {
    background-color: #f4f4f4;
    text-align: left;
  }
  
  .stats-table tr:nth-child(even) {
    background-color: #fafafa;
  }
  